  div.container {
    width: 100%;
    padding-left: 16px;
  }

  .graphic-container {
    width: 45%;
    min-width: 280px;
    float: right;
    padding: 8px 16px 8px 0;
    padding-top: 48px;
  }

  .graphic-container p {
    padding: 0;
    min-height: 140px;
  }

  .houses {
    float: left;
    padding-bottom: 30px;
  }

  @media (max-width: 660px) {
    .graphic-container {
      float: none;
      width: 75%;
      margin: 0 auto;
    }
  }

  .parks-summary {
    clear: both;
    padding-top: 48px;
  }

  .pic-container {
    position: relative;
    height: 200px;
    margin-bottom: 50px;
  }

  span {
    display: inline-block;
    padding: 4px;
  }

  span.country {
    background: #c1002a;
    color: #fff;
  }

  span.region {
    background: #0063E6;
    color: #fff;
  }

  span.la {
    background: #E6B217;
  }

  span.area {
    background: #7ec850;
  }

  li {
    padding-left: 8px;
  }

  li.country {
    border-left: 10px solid #c1002a;
  }

  li.region {
    border-left: 10px solid #0063E6;
  }

  li.la {
    border-left: 10px solid #E6B217;
  }

  li.area {
    border-left: 10px solid #7ec850;
  }

  ul {
    list-style: none;
    padding: 0;
  }

  ul.summary li {
    margin-bottom: 20px;
  }

  .legend {
    font-size: 0.75em;
  }

  .isoCheck {
    clear: both;
    padding-top: 20px;
  }
</style>